export default {
	props: {
		themeColor: {
			type: String,
			default () {
				return "#37C2BC"
			}
		},
	},
	data() {
		return {
			showPicker: false,
			itemHeight: `height: ${uni.upx2px(88)}px;`,
			confirmFlag: true,
			pickVal: [],
			checkArr: [],
			data: {
				years: [],
				months: []
			}
		}
	},
	methods: {
		initData() {
			this.data.years=[];this.data.months=[];
			for (let i = 4; i <= 15; i++) {
				this.data.years.push(i)
				this.data.months.push(i+1)
			}
			this.$set(this.checkArr, 0, this.data.years[0])
			this.$set(this.checkArr, 1, this.data.months[0])
		},
		touchStart() {
			if (this.timeout) {
				this.confirmFlag = false;
			}
		},
		touchEnd() {
			if (this.timeout) {
				setTimeout(() => {
					this.confirmFlag = true;
				}, 500)
			}
		},
		open() {
			this.showPicker = true;
			this.initData()
		},
		pickerConfirm() {
			this.$emit('confirm', this.checkArr)
			this.showPicker = false
		},
		pickerCancel() {
			this.showPicker = false
		},
		maskTap() {
			// this.$emit("cancel");
			this.showPicker = false;
		},
		bindChange(e) {
			console.log(e)
			let a = e.detail.value[0],
				m = this.data.years[a + 1],
				arry = [];
			for (let i = m; i <=15; i++) {
				arry.push(i)
			}
			this.data.months = arry;
			this.$set(this.checkArr, 0, this.data.years[a])
			if (e.detail.value.length > 1) {
				let b = e.value[1],
					n = this.data.months[b];
				this.$set(this.checkArr, 1, this.data.months[b])
			} else {
				this.$set(this.checkArr, 1, this.data.months[0])
			}
			console.log(this.checkArr)
		}
	}
}
